import {ModifyPass} from 'api/user'
export default{
	data(){
		return {
			oldpassword:'',
			/*密码*/
			password:'',
			/*再次输入*/
			rePassword:'',
			/*状态*/
			status:false,
			mobile:''
		}
	},
	watch:{
		'rePassword'(value){
			if(value == this.password){
				this.status = true
			}
		}
	},
	methods:{
		
		/*完成*/
		next(url){
			if(this.status){
				ModifyPass({
					oldpass: this.oldpassword,
					password: this.password,
					repassword: this.rePassword,
				}).then(res=>{
					if(res.result){
						uni.showToast({
							icon:'success',
							title: '修改成功'
						})
						setTimeout(()=>{
							this.$store.dispatch('getUserInfo').then(_=>{
								uni.navigateTo({
									url:'/pages/user/login/index'
								})
							})
						},2000)
						
						
					}else{
						uni.showToast({
							title: res.msg,
							icon:'none'
						})
					}
				})	
			}else{
				uni.showToast({
					title: '密码输入不一致',
					icon:'none'
				})
			}
		}
	},
	onLoad (query) { 
	    this.mobile = query.mobile
	}
}
